@import url(../../styles/Colors.css);

.container {
    margin: calc(15vh - 300px) auto 0;
    width: 540px;
    height: 600px;
}

.container__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.container__header__title {
    color: var(--Light-Grayish-Blue);
}

.change__theme__icon {
    cursor: pointer;
}

@media screen and (max-width:700px){
    .container {
        width: 80%;
    }
}
